/* 폰트 - 고딕체 */
@font-face {
    font-family: 'NanumBarunGothic';
    font-weight: 100;
    src: url('/public/fonts/NanumBarunGothic.otf') format('opentype'),
        url('/public/fonts/NanumBarunGothicUltraLight.ttf') format('truetype');
  }
@font-face {
    font-family: 'NanumBarunGothic';
    font-weight: 200;
    src: url('/public/fonts/NanumBarunGothicLight.otf') format('opentype'),
        url('/public/fonts/NanumBarunGothicLight.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'NanumBarunGothic';
    font-weight: 400;
    src: url('/public/fonts/NanumBarunGothic.otf') format('opentype'),
        url('/public/fonts/NanumBarunGothic.ttf') format('truetype');
  }
  
  @font-face {
    font-family: 'NanumBarunGothic';
    font-weight: 600;
    src: url('/public/fonts/NanumBarunGothicBold.otf') format('opentype'),
        url('/public/fonts/NanumBarunGothicBold.ttf') format('truetype');
  }

*{
    font-family: 'NanumBarunGothic';
} 

a {
    text-decoration : none;
    color: black;
}

.none {
    display: none;
}

.center {
    text-align: center;
} 

.white{
    color: white;
}

.pointer:hover{
    cursor: pointer;
}
/* 불특정 */

.test123{
    background-image: url('./../../public/img/gifBG.gif');
    width: 100%;
}
.container { 
    display: flex;
    height: 100%;
    margin: auto;
    margin-top: 50px;
    width: 85%;
    border-radius: 30px;
    height: 800px;
}

.introduce_text {
    color: white;
}

.introduce_img {
    width: 30px;
    height: 30px;
    margin: 10px;
}

.introduce_img:hover {
    cursor: pointer;
}

.content {
    border: 1px solid black;
    background-color: rgb(127, 243, 247);
    width: 90%;
    padding-left: 20px;
    border-radius: 30px;
    display: flex;
    
}

.introduce{
    margin-right: 30px;
    border-radius: 30px;
    width: 350px;
    padding : 30px 10px 10px 10px;
    text-align: center;
    /* background: red; */
}

.main{
    background: rgb(250, 237, 213);
    border: 1px solid gray;
    width: 100%;
    padding : 10px;
    border : 1px solid gray;
    border-radius: 30px;
    
}

.Propile{
    width: 200px;
    height: 200px;
    border-radius: 100px;
}

.sideMenu{ 
    margin-left: 20px;
}

.sideMenu a {
    text-decoration:none
}

.weather_box { 
    font-size: 20px;
    /* border: 1px solid black; */
    border-radius: 30%;
    color: white;
}

.another_city_search_text {
    font-size: 10px;
}

.weather_box button {
    margin-right: 3px;
    border-radius: 30%;
    border: 1px solid white;
}

.city_text{
    font-weight: 700;
}

.test button {
    border-radius: 10px;
}

.time_text{
    font-size: 12px;
}

.testwnd{
    width: 100px;
    background: white;
    border: 2px solid black;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    padding: 20px 0px;
}

.test1 {
    font-size: 30px;
    text-align: center;
    color: gray;
}

